import type { CMSFilters } from '@globalTypes/CMSFilters';

import { OFFER_CATEGORIES } from '$utils/constants';
import { createOffersInstance } from '$utils/createOffersInstance';

window.fsAttributes = window.fsAttributes || [];
window.fsAttributes.push([
  'cmsfilter',
  async (filtersInstances: CMSFilters[]) => {
    // Get the filters instance
    const offers = createOffersInstance(filtersInstances);
    await offers.getGroupCategoryOffers(Object.values(OFFER_CATEGORIES));
  },
]);
